import PetList from "./PetList";
import "./Employee.css";
import { BASE_URL } from "./EmployeeList";
import { useEffect, useState } from "react";

export const Employee = ({employee}) => {
  const pf = employee.prefix
  const fn = employee.firstName
  const ln = employee.lastName
  const psf = employee.postfix

  function fullName(){
    return `${pf ? pf : ''} ${fn} ${ln} ${psf ? psf : ''}`
  }

  const [pets, setPets] = useState([])
  useEffect(() => {
    fetch(`${BASE_URL}/pets?employeeId=${employee.id}`)
    .then(response => response.json())
    .then(json => {
      console.log(json)
      setPets(json)
    })
  }, [])

  const [show, setShow] = useState(false)

  return (
    <article className="employee">
      <h3>{fullName()}</h3>
      <h4>{employee.title}</h4>
      <button onClick={() => setShow(!show)}>{!show ? 'Show Pets' : 'Hide Pets'}</button>
      <PetList pets={pets} show={show}/>
    </article>
  );
};

export default Employee;
