export const PetList = ({pets, show}) => {
  function petsText(){
    let string = ''
    if(pets.length > 0){
      pets.forEach((pet, i) => {
        string += pet.name + (i === pets.length - 1 ? '' : ', ')
      })
      return string
    }
    else {
      return 'No pets were found for this employee.'
    }
  }

  return (
    <aside className="pets-list">
      <p>{show ? petsText() : null}</p>
    </aside>
  );
};

export default PetList;
